/**
 * Animals model events
 */

'use strict';

import { EventEmitter } from 'events'
import Animals from './animals.model'
var AnimalsEvents = new EventEmitter()

// Set max event listeners (0 == unlimited)
AnimalsEvents.setMaxListeners(0)

// Model events
var events = {
    'save': 'save',
    'remove': 'remove'
}

// Register the event emitter to the model events
for (var e in events) {
    var event = events[e]
    Animals.schema.post(e, emitEvent(event))
}

function emitEvent(event) {
    return function(doc) {
        AnimalsEvents.emit(event + ':' + doc._id, doc)
        AnimalsEvents.emit(event, doc)
    }
}

export default AnimalsEvents
